<template>
  <div>
    <b-card header-tag="header" footer-tag="footer">
      <div slot="header">
        <i class="fa fa-btc"></i>
        <strong>All addresses</strong>
      </div>

      <b-row class="mt-3" v-if="!loading">
        <b-col md="6" class="my-1">
          <b-form-group horizontal label="Filter" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Type to Search" />
              <b-input-group-append>
                <b-btn :disabled="!filter" @click="filter = ''">Clear</b-btn>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="6" class="my-1">
          <b-form-group horizontal label="Per page" class="mb-0">
            <b-form-select :options="pageOptions" v-model="perPage" />
          </b-form-group>
        </b-col>

        <b-col md="12" sm="12" class="my-2">
          <b-form-group horizontal label="Sort" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- none --</option>
              </b-form-select>

              <b-form-select :disabled="!sortBy" v-model="sortDesc" slot="append">
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>

      <div class="row">
        <div class="col-2">
          <button
            class="btn btn-success mt-3 btn-block"
            @click="archiveEmptyAddress(1)"
          >Archive addresses</button>

          <button
            class="btn btn-warning mt-3 btn-block"
            @click="getArchivedAddresses"
          >Get archived addresses</button>

          <button class="btn btn-primary mt-3 btn-block" @click="analyzeArchived">Analyze</button>
          <br />
          <br />
          <div class="text-center">
            <input type="text" name id v-model="counterInit" class="form-control" />
            <i class="fa fa-refresh" @click="nextBlock"></i>
            <input type="text" name id v-model="counterEnd" class="form-control" />
          </div>
        </div>

        <div class="col-10">
          <b-table
            v-if="!loading"
            :hover="hover"
            :striped="striped"
            :bordered="bordered"
            :small="small"
            :fixed="fixed"
            responsive="sm"
            :items="items"
            :fields="fields"
            :current-page="currentPage"
            :per-page="perPage"
            :filter="filter"
            :sort-by.sync="sortBy"
            :sort-desc.sync="sortDesc"
            :sort-direction="sortDirection"
            @filtered="onFiltered"
            class="mt-3"
          >
            <template v-slot:cell(address)="data">
              <p>{{data.item.address}}</p>
            </template>

            <template v-slot:cell(balance)="data">
              <strong class="text-success">{{data.item.available_balance}}</strong>
            </template>

            <template v-slot:cell(actions)="data">
              <button class="btn btn-sm btn-pill btn-danger">Archive</button>
            </template>
          </b-table>
          <nav>
            <b-pagination
              :total-rows="getRowCount(items)"
              :per-page="perPage"
              v-model="currentPage"
              prev-text="Prev"
              next-text="Next"
              hide-goto-end-buttons
            />
          </nav>
        </div>
      </div>
    </b-card>
  </div>
</template>

<script>
import _ from "lodash";

export default {
  props: {
    server: { type: String },
    hover: {
      type: Boolean,
      default: false
    },
    striped: {
      type: Boolean,
      default: false
    },
    bordered: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    fixed: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      items: [],
      archived: [],
      fields: [
        { key: "id", sortable: true, label: "#" },
        { key: "address", sortable: true, label: "Address" },
        { key: "balance", label: "Balance", sortable: true },
        { key: "actions", label: "Actions", sortable: false }
      ],
      currentPage: 1,
      perPage: 30,
      totalRows: 0,
      loaders: {},
      show: true,
      loading: false,
      filter: null,
      pageOptions: [5, 10, 15, 20, 25, 30, 40, 50, 150, 200, 300, 400, 500],
      sortBy: "id",
      sortDesc: true,
      sortDirection: "asc",
      totalBTC: 0,
      totalLTC: 0,
      api_key: undefined,
      api_pin: undefined,
      counterInit: 0,
      counterEnd: 100,
      apiKey: "067a-928c-6a57-030b"
    };
  },

  computed: {
    sortOptions() {
      return this.fields
        .filter(f => f.sortable)
        .map(f => {
          return { text: f.label, value: f.key };
        });
    }
  },

  methods: {
    getRowCount(items) {
      return items.length;
    },
    onFiltered(filteredItems) {
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
    nextBlock() {
      this.counterInit = parseInt(this.counterInit) + 100;
      this.counterEnd = parseInt(this.counterEnd) + 100;
    },
    archiveEmptyAddress(page) {
      this.loading = true;
      this.$http(
          "https://block.io/api/v2/get_my_addresses/?api_key=" +
            this.apiKey +
            "&page=" +
            page,
          {
            headers: {
              "content-type": "application/json"
            }
          }
        )
        .then(response => {
          response.data.data.addresses.map((address, index) => {
            address["id"] = index + 1;
            address["available_balance"] = parseFloat(
              address.available_balance
            );
            this.items.push(address);
            if (parseFloat(address.available_balance) === 0)
              this.archiveAddressNow(address.address);
          });
          if (response.data.data.has_more) this.archiveEmptyAddress(2);
          this.$toasted.show("Address archived", {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true
          });
          this.loading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },

    unArchiveAddressNow(address) {
      this.loading = true;
      this.$http(
          "https://block.io/api/v2/unarchive_address/?api_key=" +
            this.apiKey +
            "&addresses=" +
            address,
          {
            headers: {
              "content-type": "application/json"
            }
          }
        )
        .then(response => {
          console.log(response.data);
        })
        .catch(error => {
          console.log(error);
        });
    },

    archiveAddressNow(address) {
      this.loading = true;
      this.$http(
          "https://block.io/api/v2/archive_addresses/?api_key=" +
            this.apiKey +
            "&addresses=" +
            address,
          {
            headers: {
              "content-type": "application/json"
            }
          }
        )
        .then(response => {
          console.log(response.data);
        })
        .catch(error => {
          console.log(error);
        });
    },

    getArchiveAddresses(address) {
      this.loading = true;
      this.$http(
          "https://block.io/api/v2/get_my_archived_addresses/?api_key=" +
            this.apiKey +
            "",
          {
            headers: {
              "content-type": "application/json"
            }
          }
        )
        .then(response => {
          console.log(response.data);
        })
        .catch(error => {
          console.log(error);
        });
    },

    getAddresBalance(address) {
      this.loading = true;
      this.$http(
          "https://block.io/api/v2/get_address_balance/?api_key=" +
            this.apiKey +
            "&addresses=" +
            address.address,
          {
            headers: {
              "content-type": "application/json"
            }
          }
        )
        .then(response => {
          console.log(
            address.id +
              " " +
              address.address +
              ":" +
              parseFloat(response.data.data.available_balance)
          );
          if (parseFloat(response.data.data.available_balance) > 0) {
            this.unArchiveAddressNow(address.address);
            console.warn(
              address.id +
                " " +
                address.address +
                ":" +
                parseFloat(response.data.data.available_balance)
            );
          }
          // if(parseFloat(response.data.data.available_balance) > 0) console.log(parseFloat(response.data.data.available_balance))
        })
        .catch(error => {
          console.log(error);
        });
    },

    getArchivedAddresses() {
      this.loading = true;
      this.$http(
          "https://block.io/api/v2/get_my_archived_addresses/?api_key=" +
            this.apiKey +
            "",
          {
            headers: {
              "content-type": "application/json"
            }
          }
        )
        .then(response => {
          this.archived = response.data.data.addresses.map((address, index) => {
            address["id"] = index + 1;
            return address;
          });
          this.$toasted.show(
            response.data.data.addresses.length + " Total addresses archived",
            {
              position: "top-right",
              duration: 5000,
              type: "default",
              closeOnSwipe: true
            }
          );
        })
        .catch(error => {});
    },

    analyzeArchived() {
      this.archived.map((address, index) => {
        if (index >= this.counterInit && index <= this.counterEnd)
          this.getAddresBalance(address);
      });
    }
  }
};
</script>
